患者的治愈率和生存质量。2018年，原国家卫生计生委在《关于印发进一步改善医疗服务行动计划(2018至2020年)的通知》中要求医疗机构针对肿瘤、多系统多器官疾病、疑难复杂疾病等，建立多学科病例讨论和联合查房相关制度，为住院患者提供多学科诊疗服务［1］。苏州大学附属太仓医院于2017年成立了抗菌药物管理小组，针对感染性疾病建立了MDT团队。本文通过介绍临床药师参与的典型感染性病例讨论，对患者抗感染治疗中抗菌药物使用情况进行回顾性分析，并提出优化后续抗感染治疗方案的建议与分析，以探究临床药师在抗感染MDT团队中的作用。现报道如下。 ")]),_c('p',[_vm._v("1参与多重耐药菌感染治疗方案的制定")]),_c('p',[_vm._v("1．1病历资料")]),_c('p',[_vm._v(" 患者，女，58岁，因“发现肝硬化3年余，腹胀10d”于2018年7月30日入院。患者有甲状腺功能减退症、再生障碍性贫血、双下肢丹毒史，有青霉素过敏史。入院查体:T37．2℃，P114次/min，R20次/min，BP114/68mmHg。神志清，颈软，双肺呼吸音粗，未闻及干湿啰音，心律齐，腹软，下腹部膨隆，全腹无压痛及反跳痛，肝脾肋下未及，移动性浊音阳性，双下肢水肿，右脚足背皮肤发红。腹部B超:血吸虫肝病表现，门静脉海绵样变;胆囊结石，胆囊炎;脾肿大;腹腔积液。诊断肝硬化失代偿期、自身免疫性肝炎、甲状腺功能减退症、再生障碍性贫血、丹毒。患者入院当天下午出现高热，T39．8℃，右下肢疼痛不适，血常规示白细胞计数3．8×109/L，中性粒细胞0．88;C反应蛋白84mg/L;降钙素原1．01ng/ml。外科会诊考虑丹毒再发，予以莫西沙星0．4g静脉滴注每天1次。8月3日血培养提示大肠埃希菌(+)，第3、4代头孢菌素类、氟喹诺酮类耐药，换用美罗培南1g静脉滴注，每8小时1次。之后病情无明显好转，为进一步诊治，于2018年8月7日提请全院MDT。 ")]),_c('p',[_vm._v("1．2临床药师建议")]),_c('p',[_vm._v(" 患者既往有丹毒病史，此次入院丹毒再发，伴肺部感染，高热，血培养提示大肠埃希菌+，首选碳青霉烯类药物抗感染，但患者已予美罗培南1g静脉滴注每8小时1次治疗4d，体温控制不佳，建议加用氨基糖苷类联合治疗。 ")]),_c('p',[_vm._v("1．3MDT结果")]),_c('p',[_vm._v(" 根据多科室意见，患者发热与丹毒、肺部感染相关，起因为患者肝硬化失代偿的基础，但需排除腹腔内感染灶，建议痰培养，同时行下肢病灶局部超声检查了解是否存在脓肿病灶，治疗上在美罗培南基础上加用氨基糖苷类药物。 1．4后续跟踪 ")]),_c('p',[_vm._v(" 根据MDT结果，加用依替米星0．2g静脉滴注每天1次。3d后回访，患者热峰呈下降趋势。10d后复查血培养阴性，T37．5℃左右，抗菌药物降阶梯为头孢西丁2g静脉滴注每12小时1次，后感染症状逐渐好转。 1．5抗感染治疗方案分析 ")]),_c('p',[_vm._v(" 肠杆菌科细菌是临床细菌感染性疾病中最重要的致病菌，而超广谱β-内酰胺酶(ESBLs)是肠杆菌科细菌对β-内酰胺类抗菌药物产生耐药的主要机制。ESBLs可水解灭活青霉素类、氧亚氨基头孢菌素(包括第3、4代头孢菌素)及单环酰胺类氨曲南，通常不水解头霉素类和碳青霉烯类，其活性可被β-内酰胺酶抑制剂抑制。针对这类细菌引起的重症感染，专家共识［2］推荐碳青霉烯类是最有效和可靠的抗菌药物。同时提出重症感染、存在合并非发酵菌感染危险因素的患者可联合用药。该患者重症感染，使用美罗培南4d后病情好转不明显，考虑感染不易控制，故联合应用氨基糖苷类抗菌药物。经上述药物治疗后，患者病情明显好转，血培养阴性、临床症状改善后予以头霉素类降阶梯治疗。 2规范抗菌药物的使用 ")]),_c('p',[_vm._v("2．1病历资料")]),_c('p',[_vm._v(" 患者，男，67岁，因“颈部脓肿切开术后20d”于2019年2月3日入院。患者1月15日入住上海某院诊断颈部脓肿，行颈部脓肿切开引流术，术后抗炎对症治疗，2月3日颈部分泌物培养提示耐甲氧西林金黄色葡萄球菌(MRSA)，患者要求转当地医院继续治疗，上海出院医嘱建议万古霉素+美罗培南抗感染治疗。患者有哮喘病史30余年。入院查体:T36．9℃，P98次/min，R18次/min，BP122/92mmHg，神志清，颈部敷料清洁，颈前套管在位，双肺呼吸音清，未闻及明显干湿啰音。入院后给予万古霉素1g+美罗培南0．5g静脉滴注每天2次。患者2月11日出现夜间端坐呼吸，双肺听诊可及散在湿性啰音，胸部CT:双肺多发模糊影，广泛粟粒结节灶;两侧胸腔积液。血常规:白细胞计数4．6×109/L，中性粒细胞0.67;血浆D-二聚体1730μg/L;血气分析:pH值7．46，PaO290mmHg，PaCO262mmHg，氧饱和度97%;脑钠肽(BNP)1303pg/ml。为进一步诊治，于2019年2月15日提请全院MDT。 ")])])],1)]),_vm._m(0)]),_vm._m(1)])}
